<template>
  <div class="p-search-wrap">
    <header class="common-header">
      <div class="header-left">
        <v-icon name="back" class="back"></v-icon>
      </div>
      <div class="header-title">
        <input type="text" maxlength="30" v-model.trim="keyword" class="search-input" placeholder="搜索你喜欢的">
      </div>
    </header>
  </div>
</template>
<script>
export default {
  data() {
    return {
      keyword: ''
    };
  }
};
</script>
<style lang="scss">
.p-search-wrap {
  .common-header {
    position: relative;
    background-color: rgb(229, 71, 60);
    overflow: hidden;
    width: 100%;
    padding: 12px 15px 12px 30px;
    .header-left {
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      .back {
        fill: rgba(255, 255, 255, 0.6);
        width: 24px;
        height: 24px;
      }
    }
    .header-title {
      border-bottom: 1px solid hsla(0, 0%, 100%, 0.6);
      .search-input {
        display: block;
        width: 100%;
        outline: none;
        border: 0 none;
        color: hsla(0, 0%, 100%, 0.9);
        font-size: 16px;
        height: 20px;
        background-color: transparent;
        &::-webkit-input-placeholder {
          color: hsla(0, 0%, 100%, 0.6);
        }
      }
    }
  }
}
</style>



